import { reqAddOrUpdateShopCart, reqDetailList } from '@/api';

import { getUUID } from '@/utils/uuid_token';

const state = {
  //detailList:
  //
  //action
  detailList: {},
  uuid_token: getUUID()
};
const mutations = {
  GETDETAILLIST: (state, detailList) => {
    state.detailList = detailList;
  }
}
const actions = {
  //
  async getDetailList({ commit }, skuId) {
    let result = await reqDetailList(skuId);
    if (result.code == 200) {
      commit("GETDETAILLIST", result.data);
    }
  },

  async addShopCart({ commit }, { skuId, skuNum }) {
    let result = await reqAddOrUpdateShopCart(skuId, skuNum);
    console.log(result);
    if (result.code === 200) {
      return "ok"
    } else {
      return Promise.reject(new Error("faile"));
    }
  }
  //||
  // async addShopCart({ commit }, { skuId, skuNum }) {
  //   let result = await reqAddOrUpdateShopCart(skuId, skuNum);
  //   //
  //   if (result.code == 200) {
  //     //addShopCartpromise
  //     return 'ok';
  //   } else {
  //     return Promise.reject(new Error('faile'));
  //   }
  //undefinedpromise
  //undefinedundefined
  // }
};
//getters:
const getters = {
  //
  //state:gettersstatestatehomesearch
  categoryView(state) {
    //:
    //1:detailListcategoryViewundefinedundefined
    //2:detailListdetailList
    //
    return state.detailList.categoryView || {}
  },
  //
  spuSaleAttrList(state) {
    return state.detailList.spuSaleAttrList;
  },
  //
  skuInfo(state) {
    return state.detailList.skuInfo || {};
  }
};
export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}